    header {
        width: 1200px;
        margin: 0 auto;

        display: flex;
        line-height: 100px;
        justify-content: space-between;

        h1 {
            width: 150px;
            font-size: 0;
        }

        .right {
            display: flex;
            gap: 30px;

            .sel_box {
                position: relative;

                svg {
                    position: absolute;
                    top: 43%;
                    left: 10px;
                }

                select {
                    background: transparent;
                    padding: 7px 10px 7px 35px;
                    color: #fff;
                    border-radius: 5px;
                    font-size: 15px;

                    option {
                        color: #111;
                    }
                }
            }

            .login {
                a {
                    padding: 7px 15px;
                    border-radius: 5px;
                    background: #e50914;

                    &:hover {
                        background: #bd0710;
                    }
                }
            }
        }
    }

